<template>
  <div class="index-bg zui-management">
    <HeadTop :Headtype="Headtype" :title="$t('management.合約管理')">
      <template #left>
        <van-popover v-model:show="isShowChangeChain" placement="bottom-start" :actions="actionsChain" @select="onChangeChain">
          <template #reference>
            <div class="flex-center">
              {{ chain.text }}
            </div>
          </template>
        </van-popover>
      </template>
    </HeadTop>
    <div class="p15" style="overflow: hidden; padding-bottom: 30px;">
      <div class="region-titlte">
        Token：
        <select v-model="curToken" @change="tokenChangeHandle">
          <option name="">SEELE</option>
          <option :name="item.symbol" v-for="(item,index) in tokens" :key="index">{{item.symbol}}</option>
        </select>
      </div>
      <div class="manage-box">
        <div class="manage-title">{{ $t('management.最小兌換數量') }}</div>
        <van-field type="number" v-model="min" center clearable :placeholder="$t('management.輸入最小兌換數量')">
          <template #button>
            <van-button size="small" type="primary" style="width: 60px; font-size: 13px;" @click="setMinAmount">保存</van-button>
          </template>
        </van-field>
      </div>
      <div class="manage-box">
        <div class="manage-title">{{ $t('management.單筆兌換限額') }}</div>
        <van-field type="number" v-model="max" center clearable :placeholder="$t('management.輸入單筆兌換限額')">
          <template #button>
            <van-button size="small" type="primary" style="width: 60px; font-size: 13px;" @click="setMaxAmount">保存</van-button>
          </template>
        </van-field>
      </div>
      <div class="manage-box">
        <div class="manage-title">{{ $t('management.接收地址') }}</div>
        <van-field v-model.trim="recivedAddress" center clearable :placeholder="$t('management.輸入接收地址')">
          <template #button>
            <van-button size="small" type="primary" style="width: 60px; font-size: 13px;" @click="setReceiveAddress">保存</van-button>
          </template>
        </van-field>
      </div>
      <div class="manage-box">
        <div class="manage-title">{{ $t('management.手續費占比') }}</div>
        <van-field type="number" v-model="rate" center clearable :placeholder="$t('management.手續費占比提示')">
          <template #button>
            <van-button size="small" type="primary" style="width: 60px; font-size: 13px;" @click="setRate">保存</van-button>
          </template>
        </van-field>
      </div>
    </div>
  </div>
</template>
<script>
import { Toast } from 'vant';
import HeadTop from '@/components/HeadTop'
import { reactive, toRefs, computed, watch, onMounted } from "vue";
import { useRoute, useRouter, onBeforeRouteUpdate } from 'vue-router'
import { useI18n } from "vue-i18n";
import { useStore } from "vuex";
import web3Data from "@/utils/initWeb3";
import BigNumber from "bignumber.js";
import { config } from "@/utils/config";
import abiEthCross from "@/utils/ethCross";
import abiSeeleCross from "@/utils/seeleCross";
import abiTokenCross from "@/utils/tokenCross";
import tokenAbi from "@/utils/abi";
export default {
  components: {
    HeadTop
  },
  setup () {
    const store = useStore()
    const { t } = useI18n()
    const router = useRouter()
    const route = useRoute()
    const data = reactive({
      $t: t,
      Headtype: 'home',
      maxAmount: '0.00',
      min: '',
      max: '',
      recivedAddress: '',
      rate: '',
      denominator: '',
      crossContract: null,
      tokenContract: null,
      crossTokenContract: null,
      chainid: '',
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      isShowChangeChain: false,
      defaultChain: 1,
      chain: {},
      actionsChain: [
        {
          text: "Ethereum",
          value: 1
        },
        {
          text: "Seele",
          value: 186
        }
      ],
      curToken: 'SEELE',
      tokens: [],
      selectTokenInfo: {},
      decimals: 0,
      initContract: async () => {
        if (route.query) {
          data.chainid = Number(route.query.chainid)
        }
        if (data.chainid === 1) {
          // eth链主币跨seele主币
          if (data.selectTokenInfo.tokenAddress) {
            data.tokenContract = new data.web3.eth.Contract(
              tokenAbi,
              data.selectTokenInfo.tokenAddress
            );
            data.crossContract = new data.web3.eth.Contract(
              abiTokenCross, config.ethTokenAddress
            );
          } else {
            data.tokenContract = new data.web3.eth.Contract(
              tokenAbi,
              config.ethSeeleAddress
            );
            // console.log(config.ethAddress, 'config.ethAddress')
            data.crossContract = new data.web3.eth.Contract(
              abiEthCross, config.ethAddress
            );
          }
          data.crossTokenContract = new data.web3.eth.Contract(
            abiTokenCross, config.ethTokenAddress
          );
          data.decimals = await data.tokenContract.methods.decimals().call({ address: data.address })
        }
        if (data.chainid === 186) {
          // seele主币跨eth链主币
          if (data.selectTokenInfo.tokenAddress) {
            data.tokenContract = new data.web3.eth.Contract(
              tokenAbi,
              data.selectTokenInfo.tokenAddress
            );
            data.crossContract = new data.web3.eth.Contract(
              abiTokenCross, config.seeleTokenAddress
            );
            data.decimals = await data.tokenContract.methods.decimals().call({ address: data.address })
          } else {
            data.tokenContract = null
            data.crossContract = new data.web3.eth.Contract(
              abiSeeleCross, config.seeleMainAddress
            );
            data.decimals = 18
          }
          data.crossTokenContract = new data.web3.eth.Contract(
            abiTokenCross, config.seeleTokenAddress
          );
        }
        data.initData()
      },
      initData: async () => {
        data.min = ''
        data.max = ''
        data.recivedAddress = ''
        data.gasnum = ''
        data.getTokens()
        let owner = await data.crossContract.methods.owner().call({ address: data.address })
        if (owner === data.address) {
          data.getDenominator()
          data.getMaxAmount()
          data.getMinAmount()
        } else {
          console.error('The current address is not the owner')
        }
      },
      getDenominator: async () => {
        let denominator = await data.crossContract.methods.denominator().call({ address: data.address })
        // console.log(denominator, 'denominator')
        data.denominator = denominator
        let rate = await data.crossContract.methods.rate().call({ address: data.address });
        data.rate = rate
      },
      getMaxAmount: async () => {
        let maxAmount = await data.crossContract.methods.maxAmount().call({ address: data.address })
        // console.log(maxAmount, 'maxAmount')
        let decimals = data.decimals
        data.max = new BigNumber(maxAmount)
          .dividedBy(10 ** decimals).toFixed(0)
      },
      getMinAmount: async () => {
        let decimals = data.decimals
        let minAmount = await data.crossContract.methods.minAmount().call({ address: data.address })
        minAmount = new BigNumber(minAmount).dividedBy(10 ** decimals).toFixed(0)
        data.min = minAmount
      },
      setMinAmount: async () => {
        if (!data.address) {
          Toast(data.$t('management.請先連接錢包'))
          return
        }
        if (!data.min) {
          Toast(data.$t('management.輸入最小兌換數量'))
          return
        }
        if (Number(data.min) < 0) {
          Toast(data.$t('management.最小數量需大於0'))
          return
        }
        let decimals = data.decimals
        let minAmount = new BigNumber(data.min).multipliedBy(10 ** decimals).toFixed()
        // console.log(minAmount, 'minAmount')
        const toast0 = Toast.loading({
          duration: 0,
          forbidClick: true,
          message: '保存中...',
        });
        await data.crossContract.methods.setMinAmount(
          minAmount
        ).send({
          from: data.address
        }).then(() => {
          toast0.clear()
          Toast(data.$t('management.保存成功'))
          data.initData()
          // console.log(receipt)
        }).catch(() => {
          toast0.clear()
          Toast(data.$t('management.保存失敗'))
        })
      },
      setMaxAmount: async () => {
        if (!data.address) {
          Toast(data.$t('management.請先連接錢包'))
          return
        }
        if (!data.max) {
          Toast(data.$t('management.輸入單筆兌換限額'))
          return
        }
        if (Number(data.max) < 0) {
          Toast(data.$t('management.最小數量需大於0'))
          return
        }
        let decimals = data.decimals
        let maxAmount = new BigNumber(data.max).multipliedBy(10 ** decimals).toFixed()
        const toast0 = Toast.loading({
          duration: 0,
          forbidClick: true,
          message: '保存中...',
        });
        await data.crossContract.methods.setMaxAmount(
          maxAmount
        ).send({
          from: data.address
        }).then(() => {
          toast0.clear()
          Toast(data.$t('management.保存成功'))
          data.initData()
        }).catch(err => {
          toast0.clear()
          Toast(data.$t('management.保存失敗'))
          console.error(err)
        })
      },
      setRate: async () => {
        if (!data.address) {
          Toast(data.$t('management.請先連接錢包'))
          return
        }
        if (!data.rate) {
          Toast(data.$t('management.請輸入手續費占比'))
          return
        }
        if (Number(data.rate) < 0) {
          Toast(data.$t('management.最小數量需大於0'))
          return
        }
        if (Number(data.rate) > Number(data.denominator)) {
          Toast(data.$t('management.手續費占比不能大於') + data.denominator)
          return
        }
        const toast2 = Toast.loading({
          duration: 0,
          forbidClick: true,
          message: '保存中...',
        });
        await data.crossContract.methods.setRate(
          data.rate
        ).send({
          from: data.address
        }).then(() => {
          toast2.clear()
          Toast(data.$t('management.保存成功'))
          data.initData()
        }).catch(err => {
          toast2.clear()
          Toast(data.$t('management.保存失敗'))
          console.error(err)
        })
      },
      setReceiveAddress: async () => {
        if (!data.address) {
          Toast(data.$t('management.請先連接錢包'))
          return
        }
        if (!data.recivedAddress) {
          Toast(data.$t('management.輸入接收地址'))
          return
        }
        const toast1 = Toast.loading({
          duration: 0,
          forbidClick: true,
          message: '保存中...',
        });
        await data.crossContract.methods.setReceiveAddress(
          data.recivedAddress
        ).send({
          from: data.address
        }).then(() => {
          toast1.clear()
          Toast(data.$t('management.保存成功'))
          data.initData()
        }).catch(err => {
          toast1.clear()
          Toast(data.$t('management.保存失敗'))
          console.error(err)
        })
      },
      onChangeChain: item => {
        data.chain = item;
        // console.log(item.value, 'xxxxxxxxxx')
        let chainid = item.value
        web3Data.switchChain(chainid)
        router.replace({ name: 'Index', query: { 'chainid': chainid } })
      },
      tokenChangeHandle: (e) => {
        if (e.target.value === 'SEELE') {
          data.selectTokenInfo = {}
          data.initContract()
        } else {
          let _tokens = data.tokens
          for (let k = 0; k < _tokens.length; k++) {
            if (e.target.value === _tokens[k]['symbol']) {
              data.selectTokenInfo = _tokens[k]
              data.initContract()
              break
            }
          }
        }
      },
      getTokens: async () => {
        try {
          let getAllToken = await data.crossTokenContract.methods.getAllToken().call({ address: data.address });
          // console.log(getAllToken, 'getAllToken')
          let _tokens = []
          if (getAllToken.length) {
            for (let k = 0; k < getAllToken.length; k++) {
              _tokens.push({
                tokenAddress: getAllToken[k]['tokenAddress'],
                decimals: getAllToken[k]['decimals'],
                symbol: getAllToken[k]['symbol'].toUpperCase(),
              })
            }
          }
          data.tokens = _tokens
        } catch (error) {
          console.log(error, 'getAllToken')
        }
      }
    })
    if (data.address) {
      data.initContract()
    }
    onBeforeRouteUpdate(async (to) => {
      if (Object.prototype.hasOwnProperty.call(to.query, 'chainid')) {
        data.chainid = Number(to.query.chainid)
      } else {
        data.chainid = data.defaultChain
      }
    })
    onMounted(async () => {
      let curId = await web3Data.web3.eth.net.getId()
      console.log(curId, 'curId')
      let ids = web3Data.chainIds
      if (ids.indexOf(curId) !== -1) {
        if (curId === 186) {
          data.chain = {
            text: "Seele",
            value: 186
          }
        } else {
          data.chain = {
            text: "Ethereum",
            value: 1
          }
        }
        // console.log('curId getId', curId);
        web3Data.switchChain(curId)
        router.replace({ name: 'Index', query: { 'chainid': curId } })
      } else {
        // console.log('curId defaultChain', data.defaultChain);
        web3Data.switchChain(data.defaultChain)
        router.replace({ name: 'Index', query: { 'chainid': data.defaultChain } })
      }
    });
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less">
.zui-management {
  .van-cell {
    background: #f9f9f9;
    border-radius: 10px;
  }
}
</style>
<style lang="less" scoped>
.index-bg {
  background: url(~@/assets/images/index-bg.jpg) no-repeat center top 46px;
  background-size: 100% auto;
  font-family: PingFang SC;
  .manage-box {
    margin-top: 15px;
    box-sizing: border-box;
    padding: 0 15px 20px;
    background: #ffffff;
    box-shadow: -1px 2.5px 10px 0px rgba(191, 211, 239, 0.38);
    border-radius: 10px;
    .manage-title {
      font-size: 14px;
      font-weight: bold;
      color: #111111;
      line-height: 1;
      padding: 17.5px 0 12px;
    }
  }
  .region-titlte {
    padding: 12px 0 0;
  }
}
</style>
